.detail {
    display: flex;
    background-color: #f4f4f4;
    max-width: 90vw;
    height: 90;
    width: 750px;
    min-height: 80px;
    margin: auto;
    padding: 15px;
    vertical-align: middle;
    justify-content: center;
    flex-direction: row;
}

.detail img {
    margin: auto;
    vertical-align: middle;
    text-align: center;
}


.result .caption h4 {
    text-transform: uppercase;
    margin: 10px auto;
    line-height: 19.8px;
    font-size: 18px;
}

.result .caption p {
    margin: 0;
    margin-bottom: 10px;
    line-height: 22.8571px;
    font-size: 16px;
}

.result .caption p small {
    padding-right: 10px;
    line-height: 19.4286px;
    font-size: 13.6px;
}

@media (min-width: 768px) {
    .detail img {
        width: 750px;
    }
}